import React, { useEffect, useMemo, useState } from 'react'
import { useLocation } from 'react-router-dom'
import { useWeb3React } from '@web3-react/core'
import { useQueryParams, StringParam, NumberParam, ArrayParam, BooleanParam, withDefault } from 'use-query-params'
import { useSelector } from 'react-redux'
import styled from 'styled-components'
import axios from 'axios'
import Box from 'components/Box'
import Pagination from 'components/Pagination'
import Flex from 'components/Flex'
import PageLoading from 'components/PageLoading'
import { CardDetailModal, useModal } from 'components/Modal'
import { API_CYBALL, ZERO_ADDRESS } from 'config/constant'
import NFTDetail from 'pages/Inventory/NFTDetail'
import { useAppDispatch } from 'state'
import { setSelectedNftId } from 'state/reducers/cybloc'
import { selectIsLoadingConfig, selectTypeConfig } from 'state/reducers/app'
import { useApi } from './api'
import LeftFilter from './components/LeftFilter'
import SearchByCyblocId from './components/LeftFilter/SearchCyblocId'
import NFTsGrid from '../../components/NFTsGrid'
import ExploreFilterHeader from './components/ExploreFilterHeader'
import { getFiltersFromUrl } from './util/filterHelper'

const Wrapper = styled(Box)`
  min-height: inherit;
`
const Container = styled(Flex)`
  position: relative;
  max-width: 1400px;
  margin: 0 auto;
  margin-top: 30px;

  @media (max-width: 1000px) {
    max-width: 90%;
  }
  @media (max-width: 576px) {
    max-width: 100%;
  }
`

const DEFAULT_FILTERS = {
  type: 1,
  page: {
    curOffset: null,
    curPage: 0,
    numPages: null,
    pageSize: 16,
    total: null,
  },
  searchText: null,
  class: [],
  trait: {
    type: null,
    traitFilteredList: [],
  },
  id: '',
  skill: {
    shooting: {
      min: 0,
      max: 100,
    },
    crossing: {
      min: 0,
      max: 100,
    },
    passing: {
      min: 0,
      max: 100,
    },
    tackling: {
      min: 0,
      max: 100,
    },
    dribbling: {
      min: 0,
      max: 100,
    },
    physical: {
      min: 0,
      max: 100,
    },
  },
  showMyAuction: false,
  mentoring: null,
  country: null,
  color: null,
  payToken: null,
  sortFilterOption: 1,
}

const Body = styled(Box)`
  position: relative;
  width: 100%;
  align-items: flex-start;
`

const ExploreAll = styled(Box)`
  overflow-y: visible;
  padding: 5px;
`
const DEFAUL_CYBLOCS_PER_PAGE = 40

const Gallery = () => {
  const { getCyblocList } = useApi()
  const dispatch = useAppDispatch()
  const [cyblocList, setCyblocList] = useState([])
  const [total, setTotal] = useState(0)
  const [isLoading, setIsLoading] = useState(true)
  const isLoadingConfig = useSelector(selectIsLoadingConfig)
  const [isLoadingFirstTime, setIsLoadingFirstTime] = useState(true)
  const [onPresentViewCyBlocDetailModal, onDimiss] = useModal(
    <CardDetailModal>
      <NFTDetail
        isPreview
        onDismiss={() => {
          onDimiss()
        }}
      />
    </CardDetailModal>,
  )
  const [query, setQuery] = useQueryParams({
    pageNumber: NumberParam,
    showMyAuction: BooleanParam,
    cyblocId: NumberParam,
    classType: withDefault(ArrayParam, []),
    shootingMin: NumberParam,
    shootingMax: NumberParam,
    crossingMin: NumberParam,
    crossingMax: NumberParam,
    tacklingMin: NumberParam,
    tacklingMax: NumberParam,
    passingMin: NumberParam,
    passingMax: NumberParam,
    dribblingMin: NumberParam,
    dribblingMax: NumberParam,
    physicalMin: NumberParam,
    physicalMax: NumberParam,
    traitType: withDefault(ArrayParam, []),
    traitId: withDefault(ArrayParam, []),
    sort: NumberParam,
    payToken: StringParam,
  })
  const { pageNumber = 1, cyblocId } = query
  useEffect(() => {
    // const newFilters = getFiltersFromUrl(DEFAULT_FILTERS, query)
    const fetchCyblocs = async () => {
      setIsLoading(true)
      const { cyblocs, total: totalCybloc } = await getCyblocList(
        ZERO_ADDRESS,
        DEFAUL_CYBLOCS_PER_PAGE,
        pageNumber === 1 ? 1 : (pageNumber - 1) * DEFAUL_CYBLOCS_PER_PAGE + 1,
      )
      setCyblocList(cyblocs)
      setTotal(totalCybloc)
      setIsLoadingFirstTime(false)
      setIsLoading(false)
    }
    if (pageNumber && !cyblocId) {
      fetchCyblocs()
    }
  }, [pageNumber, cyblocId])

  useEffect(() => {
    const fetchNftDetail = async (tokenId) => {
      try {
        const { data } = await axios.get(`${API_CYBALL}/cyblocs/${tokenId}`, {
          headers: {
            'x-functions-key': process.env.REACT_APP_AZURE_CODE,
          },
        })
        if (data) {
          setCyblocList([data])
          setIsLoadingFirstTime(false)
          setIsLoading(false)
        }
      } catch (error) {
        setIsLoading(false)
        setCyblocList([])
      }
    }
    if (cyblocId) {
      setIsLoading(true)
      fetchNftDetail(cyblocId)
    }
  }, [cyblocId])

  return (
    <Wrapper>
      {isLoadingConfig || isLoadingFirstTime ? (
        <PageLoading />
      ) : (
        <Container>
          <LeftFilter setQuery={setQuery} query={query} />
          <Body ml="34px">
            <ExploreFilterHeader setQuery={setQuery} query={query} />
            <ExploreAll mt="16px" mb="60px">
              <NFTsGrid
                loading={isLoading}
                isPreview
                items={cyblocList}
                numPerRow={null}
                onClickItem={(item) => {
                  dispatch(setSelectedNftId(item.tokenId))
                  onPresentViewCyBlocDetailModal()
                }}
              />
            </ExploreAll>
            {!query.cyblocId && !isLoading && (
              <Pagination
                currentPage={pageNumber}
                totalCount={Number(total)}
                pageSize={DEFAUL_CYBLOCS_PER_PAGE}
                onPageChange={(page) =>
                  setQuery({
                    pageNumber: page,
                  })
                }
              />
            )}
          </Body>
        </Container>
      )}
    </Wrapper>
  )
}

export default Gallery
