import React, { useState, useMemo, useEffect } from 'react'
import styled from 'styled-components'
import Dropdown from 'components/Dropdown'
import Flex from 'components/Flex'
import Box from 'components/Box'
import Text from 'components/Text'
import { useApi } from 'pages/Marketplace/api'
import { formatCurrencyVolumeSummary } from 'utils/formatCurrency'
import { Collapse } from 'react-collapse'

const Wrapper = styled(Flex)`
  position: relative;
  max-width: 1400px;
  margin: 0 auto;
  margin-top: 30px;
  align-items: center;
  flex-direction: column;

  @media (max-width: 1000px) {
    max-width: 90%;
  }
  @media (max-width: 576px) {
    max-width: 100%;
  }
`

const Title = styled(Text)`
  // // min-width: 126px;
  position: relative;
  font-style: normal;
  font-weight: 500;
  font-size: 1.3rem;
  color: #e0e0e0;
`
const ValueText = styled(Title)`
  font-weight: 600;
  font-size: 1.6rem;
`

const BG = styled(Flex)`
  justify-content: center;
  align-items: center;
  position: relative;
  height: 70px;
  clip-path: polygon(17px 0, 100% 0, calc(100% - 18px) 100%, 0% 100%, 0% 34px);
  transition: all 0.3s ease-out;
  background: linear-gradient(180deg, #b596dc 0%, #3d296a 50%);
  &::before {
    content: '';
    display: block;
    position: absolute;
    top: 2px;
    left: 2px;
    height: calc(100% - 4px);
    width: calc(100% - 4px);
    clip-path: polygon(16.5px 0, 99.6% 0, calc(100% - 18px) 100%, 0 100%, 0 33.5px);
    background: #4c3886;
  }
`

const VolumeIndicator = () => {
  const { getVolumeSummary } = useApi()
  const VOLUME_INDICATOR_OPTIONS = useMemo(() => {
    return [
      {
        symbol: 'Last 7 Days',
      },
      {
        symbol: 'Last 30 Days',
      },
      {
        symbol: 'All Time',
      },
    ]
  }, [])
  const [volumeUnit, setVolumeUnit] = useState(VOLUME_INDICATOR_OPTIONS[0])

  const [data, setData] = useState(null)

  useEffect(() => {
    async function fetchVolumeSummary() {
      const resData = await getVolumeSummary()
      if (resData) {
        switch (volumeUnit) {
          case 'Last 7 Days':
            setData([
              { title: 'Items', value: resData.quantityAuctions },
              { title: 'Volume', value: formatCurrencyVolumeSummary(resData.vol7d) },
              { title: 'Floor Price', value: formatCurrencyVolumeSummary(resData.floorPrice) },
              { title: 'Total Supply', value: resData.totalSupply },
            ])

          // eslint-disable-next-line no-fallthrough
          case 'Last 30 Days':
            setData([
              { title: 'Items', value: resData.quantityAuctions },
              { title: 'Volume', value: formatCurrencyVolumeSummary(resData.vol30d) },
              { title: 'Floor Price', value: formatCurrencyVolumeSummary(resData.floorPrice) },
              { title: 'Total Supply', value: resData.totalSupply },
            ])
          // eslint-disable-next-line no-fallthrough
          default:
            setData([
              { title: 'Items', value: resData.quantityAuctions },
              { title: 'Volume', value: formatCurrencyVolumeSummary(resData.vol) },
              { title: 'Floor Price', value: formatCurrencyVolumeSummary(resData.floorPrice) },
              { title: 'Total Supply', value: resData.totalSupply },
            ])
        }
      }
    }
    fetchVolumeSummary()
  }, [volumeUnit])

  return data ? (
    <Collapse isOpened>
      <Wrapper>
        <Box width="165px">
          <Dropdown
            position="left"
            options={VOLUME_INDICATOR_OPTIONS}
            value={volumeUnit}
            onHandleChange={(value) => {
              setVolumeUnit(value)
            }}
          />
        </Box>
        <Flex style={{ flexWrap: 'wrap', marginTop: '20px', justifyContent: 'center', marginBottom: '80px' }}>
          {data.map((el) => {
            return (
              <BG margin="10px 2px" width="270px">
                <Flex style={{ flexDirection: 'column', alignItems: 'center' }}>
                  <Title>{el.title}</Title>
                  <ValueText mt="6px">{el.value}</ValueText>
                </Flex>
              </BG>
            )
          })}
        </Flex>
      </Wrapper>
    </Collapse>
  ) : (
    <></>
  )
}

export default VolumeIndicator
