import axios from 'axios'
import { NETWORK, API_CYBALL } from 'config/constant'

// eslint-disable-next-line no-undef

export const useApi = () => {
  const getSortFilterOption = (option) => {
    switch (option) {
      case 2:
        return { sortBy: 'priceIndex', sortDir: 1 }
      case 3:
        return { sortBy: 'priceIndex', sortDir: -1 }
      default:
        return null
    }
  }

  const getSkillFilter = (skill) => {
    let newskillFilter = {}
    const { shooting, crossing, passing, tackling, dribbling, physical } = skill

    if (shooting.min !== 0 || shooting.max !== 100) {
      newskillFilter = { ...newskillFilter, shootingMin: shooting.min, shootingMax: shooting.max }
    }
    if (crossing.min !== 0 || crossing.max !== 100) {
      newskillFilter = { ...newskillFilter, crossingMin: crossing.min, crossingMax: crossing.max }
    }
    if (passing.min !== 0 || passing.max !== 100) {
      newskillFilter = { ...newskillFilter, passingMin: passing.min, passingMax: passing.max }
    }
    if (tackling.min !== 0 || tackling.max !== 100) {
      newskillFilter = { ...newskillFilter, tacklingMin: tackling.min, tacklingMax: tackling.max }
    }
    if (dribbling.min !== 0 || dribbling.max !== 100) {
      newskillFilter = { ...newskillFilter, dribblingMin: dribbling.min, dribblingMax: dribbling.max }
    }
    if (physical.min !== 0 || physical.max !== 100) {
      newskillFilter = { ...newskillFilter, physicalMin: physical.min, physicalMax: physical.max }
    }

    return newskillFilter
  }

  const getCyblocList = async (account, limit, from) => {
    try {
      const { data } = await axios.get(`${API_CYBALL}/cyblocs`, {
        params: {
          address: account,
          limit,
          from,
        },
      })

      return data
    } catch (error) {
      console.log(error, 'Fail to fetch cybloc list')
      return null
    }
  }

  const getTokensNft = async (filters, account) => {
    try {
      const sortOption = getSortFilterOption(filters.sortFilterOption)
      const {
        page: { pageSize, curPage },
        class: classInfo,
        id,
        trait: { type, traitFilteredList },
        skill,
        showMyAuction,
        payToken,
      } = filters
      // const { shooting, crossing, passing, tackling, dribbling, physical } = skill
      const typeIds = classInfo.map((classItem) => classItem.id)
      const res = await axios.get(`${apiUrl}/cyblocs?network=${NETWORK}`, {
        headers: {
          'x-functions-key': process.env.REACT_APP_AZURE_CODE,
        },
        params: {
          pageNum: curPage,
          pageSize,
          cyblocId: id,
          type: typeIds,
          payToken,
          sellerAddress: showMyAuction ? account : null,
          ...getSkillFilter(skill),

          ...(type === TRAIT_TYPE.NAME ? { traitType: traitFilteredList } : { traitId: traitFilteredList }),
          ...(sortOption != null ? sortOption : null),
        },
      })
      return res.data
    } catch (error) {
      console.log(error, 'error ?')
      return []
    }
  }

  return {
    getTokensNft,
    getCyblocList,
  }
}
